type TDiscount = {
  cover: string;
  storeName: string;
  discountText: string;
};

type TProps = {
  discounts: TDiscount[];
};

function Discount({ data }: { data: TDiscount }) {
  return <div>{data.discountText}</div>;
}
export default function Discounts({ discounts }: TProps) {
  return (
    <div className="container">
      {discounts.map((discount, idx) => (
        <Discount key={`${idx}: ${discount.storeName}`} data={discount} />
      ))}
      <style jsx>{`
        .container {
          border-radius: 15px;
          padding: 16px;
          background-color: white;
        }
      `}</style>
    </div>
  );
}
